export default [{
    path: '/shift-password',
    name: 'ShiftPassword',
    meta: {
      layout: 'form',
      notRequiresAuth: true,
      title: 'Изменить пароль'
    },
    component: () => import('@/pages/Shift/ShiftPassword.vue')
  },
  {
    path: '/shift-email',
    name: 'ShiftEmail',
    meta: {
      layout: 'form',
      notRequiresAuth: true,
      title: 'Изменить Email'
    },
    component: () => import('@/pages/Shift/ShiftEmail.vue')
  },
  {
    path: '/shift-email-success',
    name: 'ShiftEmailSuccess',
    meta: {
      layout: 'form',
      notRequiresAuth: true,
      title: 'Запрос на изменение Email'
    },
    component: () => import('@/pages/Shift/ShiftEmailSuccess.vue')
  },
  {
    path: '/change-email-success',
    name: 'ChangeEmailSuccess',
    meta: {
      layout: 'form',
      notRequiresAuth: true,
      title: 'Успешное изменение Email'
    },
    component: () => import('@/pages/Shift/ChangeEmailSuccess.vue')
  },
  {
    path: '/shift-email-failed',
    name: 'ShiftEmailFailed',
    meta: {
      layout: 'form',
      notRequiresAuth: true,
      title: 'Email не изменен'
    },
    component: () => import('@/pages/Shift/ShiftEmailFailed.vue')
  }

]



