import axios from 'axios'
import store from '@/store'

const NODE_ENV = process.env.NODE_ENV ? process.env.NODE_ENV : 'development';

axios.defaults.headers['content-type'] = 'application/json';
axios.defaults.withCredentials = true;
switch (NODE_ENV) {
  case 'development':
    axios.defaults.baseURL =  'https://team13.herokuapp.com/api/v1/';
    break;
  case 'production':
    axios.defaults.baseURL =  'https://team13.herokuapp.com/api/v1/';
    break;
  default:
    axios.defaults.baseURL =  'https://team13.herokuapp.com/api/v1/';
}

const token = localStorage.getItem('user-token')
if (token) axios.defaults.headers.common['Authorization'] = token

axios.interceptors.response.use(null, (error) => {
  let message = ''

  if (error.response.data.error === 'invalid_request') {
    message = error.response.data.error_description
  } else {
    message = error.response.data.path + ' - ' + error.response.data.error
  }

  if (error.response.status === 403) {

    localStorage.removeItem('user-token')
    delete axios.defaults.headers.common['Authorization']
    this.$router.push({name: 'Login'})
  } else {
    store.dispatch('global/alert/setAlert', {
      status: 'error',
      text: message
    })

    if (error.response.status === 401) {
      store.dispatch('auth/api/logout')
      window.location.reload()
    }
  }

  console.error(error)
  //return Promise.reject(error)
});



