import axios from 'axios'
import router from '../../router'

export default {
  namespaced: true,
  state: {
    notifications: [{
        icon: 'comments',
        name: 'О новых комментариях к моим публикациям',
        type: 'POST_COMMENT',
        enable: false
      },
      {
        icon: 'reviews',
        name: 'О ответах на мои комментарии',
        type: 'COMMENT_COMMENT',
        enable: false
      },
      {
        icon: 'friends',
        name: 'О заявках в дузья',
        type: 'FRIEND_REQUEST',
        enable: false
      },
      {
        icon: 'messages',
        name: 'О новых личных сообщениях',
        type: 'MESSAGE',
        enable: false
      },
      {
        icon: 'birthdays',
        name: 'О дне рождения друга',
        type: 'FRIEND_BIRTHDAY',
        enable: false
      }
    ],
  },
  getters: {
    getNotificationsSettings: s => s.notifications
  },
  mutations: {
    setNotificationsSettings: (s, notifications) => s.notifications.map(el => el.enable = notifications.find(n => n.type === el.type).enable)
  },
  actions: {
    async passwordRecovery({
      dispatch
    }, email) {
      await axios({
        url: 'account/password/recovery',
        method: 'PUT',
        data: email
      }).then(async response => {
        dispatch('global/alert/setAlert', {
          status: 'success',
          text: 'Ссылка отправлена'
        }, {
          root: true
        })
        router.push({ name: 'ForgotSuccess' });
      }).catch(error => {
        dispatch('global/alert/setAlert', {
          status: 'error',
          text: 'Не удалось отправить ссылку'
        }, {
          root: true
        })
        router.push({ name: 'ForgotFailed' });
      })
    },
    async emailShift({
        dispatch
        }, email) {
      await axios({
        url: 'account/email/shift',
        method: 'PUT',
        data: email
      }).then(async response => {
        dispatch('global/alert/setAlert', {
          status: 'success',
          text: 'Запрос отправлен'
        }, {
          root: true
        })
        router.push({ name: 'ShiftEmailSuccess' });
      })
    },
    async passwordSet({
      dispatch
    }, password) {
      let data = {
        //test
        token: localStorage.getItem('recovery-token'),
        password
      }
      await axios({
        url: 'account/password/set',
        method: 'PUT',
        data
      }).then(async response => {
        dispatch('global/alert/setAlert', {
          status: 'success',
          text: 'Пароль успешно изменен'
        }, {
          root: true
        })
        router.push({ name: 'ChangePasswordSuccess' });
      }).catch(error => {
        router.push({ name: 'ChangePasswordFailed' });
      })
    },
    //test
    async passwordShift({
        dispatch
      }, user) {
      let data = {
        token: localStorage.getItem('user-token'),
        password: user.password
      }
      await axios({
        url: 'account/password/set',
        method: 'PUT',
        data
      }).then(async response => {
        dispatch('global/alert/setAlert', {
          status: 'success',
          text: 'Пароль успешно изменен'
        }, {
          root: true
        })
        localStorage.removeItem('user-token')
        delete axios.defaults.headers.common['Authorization']
        router.push({name: 'ChangePasswordSuccess'})
      }).catch(error => {
        router.push({ name: 'ChangePasswordFailed' })
      })
    },
    changeNotifications({
      dispatch
    }, data) {
      axios({
        url: 'account/notifications',
        method: 'PUT',
        data
      }).then(response => {
        dispatch('global/alert/setAlert', {
          status: 'success',
          text: 'Настройки уведомления изменены'
        }, {
          root: true
        })
        dispatch('apiNotificationsSettings')
      }).catch(error => {})
    },
    async apiNotificationsSettings({
      commit
    }) {
      await axios({
        url: 'account/notifications',
        method: 'GET'
      }).then(response => {
        commit('setNotificationsSettings', response.data.data)
      }).catch(error => {})
    }
  }
}



